import React from 'react';
import {Link} from 'react-router-dom'

import './nav.css';

export default function NavigationBar(props) {
	return (
		<header className="nav">
			<nav className="">
				<span className="link" href="#" title="Home"><Link to="/">Home</Link></span>
				<span className="link" href="#" title="Sign in"><Link to="/dashboard">Sign in</Link></span>
			</nav>
		</header>
	);
}


